<template>
    <yd-layout class="schBg">
        <div class="handleTabBox">
            <yd-flexbox>
                <yd-flexbox-item>
                    <div class="tabTopL">会议</div>
                </yd-flexbox-item>
                <yd-flexbox-item>
                    <div @click="closeWebView" class="tabTopR"><img class="imgWork" src="../../../static/images/home@2x.png">工作中心</div>
                </yd-flexbox-item>
            </yd-flexbox>
            <div class="tabBottom">
                <router-link to="/handle"><span class="tabIcon">待办</span></router-link>
                <router-link to="/performanceList"><span class="tabIcon">绩效</span></router-link>
                <router-link to=""><span class="tabIcon active">会议</span></router-link>
                <span class="tabIcon" @click="jumpSchedule()">日程</span>
            </div>
        </div>
        <div class="schedules main_sche">
            <div class=" handleList" v-if="unStartList.length<1&&notStartFlag">
                <div class="noBox">
                    <img src="../../../static/images/label/noHandle.png">
                </div>
                <p class="noWord">您暂时没有会议待处理</p>
            </div>
            <div class=" handleList" v-if="endedList.length<1&&!notStartFlag">
                <div class="noBox">
                    <img src="../../../static/images/label/noHandle.png">
                </div>
                <p class="noWord">您暂时没有会议待处理</p>
            </div>
            <div class="mainContent pubList" v-for="(item, index) in unStartList" :key="item.meetTitle" v-show="notStartFlag">
                <div class="scheduleDetail">
                    <div class="topMess" @click="jumpPage(item.meetingID,item.advanceTime)">
                        <div class="title" v-text="item.meetTitle" v-if="item.state==1"></div>
                        <div class="title nosure" v-if="item.state==0">{{item.meetTitle}}</div>
                        <span class="status" v-if="item.advanceTime == 0">现在</span>
                        <span class="status" v-if="item.advanceTime<60&&item.advanceTime>0">{{item.advanceTime}}分钟后</span>
                        <span class="status" v-if="item.advanceTime >=60&&item.advanceTime<24*60">{{parseInt(item.advanceTime/60)}}小时后</span>
                        <span class="status" v-if="item.advanceTime>=24*60">{{parseInt(item.advanceTime/1440)}}天后</span>
                        <div class="time">时间：{{item.meetBeginTime}}-{{formatDate(item.meetEndTime, 'hh:mm')}}</div>
                        <div class="add" v-text="item.meetAddress"></div>
                    </div>
                    <div class="bottomBox">
                        <ul>
                            <li class="share" v-if="item.isVIP!=''&&item.isVIP!=null&&item.isVIP!=undefined">公司领导</li>
                            <li class="edit" v-if="item.isMeetingFile!=0">会议材料</li>
                            <li class="video" v-if="item.isVideoMeeting=='1'">视频会议</li>
                        </ul>
                    </div>
                </div>
            </div>
            <div class="mainContent pubList" v-for="(item, index) in endedList" :key="index" v-show="!notStartFlag">
                <div class="scheduleDetail">
                    <div class="topMess" @click="jumpPage(item.meetingID,item.advanceTime)">
                        <div class="title" v-text="item.meetTitle"></div>
                        <span class="status" v-if="item.advanceTime == 0">现在</span>
                        <span class="status" v-if="item.advanceTime > 0">{{parseInt(item.advanceTime/60)}}小时后</span>
                        <div class="time">时间：{{item.meetBeginTime}}-{{formatDate(item.meetBeginTime, 'hh:mm')}}</div>
                        <div class="add" v-text="item.meetAddress"></div>
                    </div>
                    <div class="bottomBox">
                        <ul>
                            <li class="share" v-if="item.isVIP!=''&&item.isVIP!=null&&item.isVIP!=undefined">公司领导</li>
                            <li class="edit" v-if="item.isMeetingFile!=0">会议材料</li>
                            <li class="video" v-if="item.isVideoMeeting=='1'">视频会议</li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
        <yd-tabbar class="schedules" slot="tabbar" bgcolor="#F4F4F2" activeColor="#369df4" color="#999999">
            <yd-tabbar-item link="" title="未开始" active @click.native="notStartFlag=true" :active="notStartFlag">
                <yd-icon name="unStart" slot="icon" size="0.54rem" v-show="notStartFlag"></yd-icon>
                <yd-icon name="start" slot="icon" size="0.54rem" v-show="!notStartFlag"></yd-icon>
            </yd-tabbar-item>
            <yd-tabbar-item title="已结束/不参加" link="" @click.native="notStartFlag=false" :active="!notStartFlag">
                <yd-icon name="unAttend" slot="icon" size="0.54rem" v-show="notStartFlag"></yd-icon>
                <yd-icon name="attend" slot="icon" size="0.54rem" v-show="!notStartFlag"></yd-icon>
            </yd-tabbar-item>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'meetingManage',
    data() {
        return {
            notStartFlag: true,
            unStartList: [],
            endedList: []
        }
    },
    mounted() {
        let vm = this;
        document.setTitle('会议管理');
        vm.initData();
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo'
        })
    },
    methods: {
        initData() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/mobileMeetissueList', {
                'curPageIndex': 1,
                'name': vm.userInfo.ssotoken
            }, function (res) {
                console.log(res);
                if (res.jsonData != null) {
                    for (let i = 0; i < res.jsonData.length; i++) {
                        if (res.jsonData[i].advanceTime >= 0 && res.jsonData[i].state != 3 && res.jsonData[i].meetState != 8) {
                            vm.unStartList.push(res.jsonData[i]);
                        } else {
                            vm.endedList.push(res.jsonData[i]);
                        }
                    }
                }
                vm.unStartList;
                vm.endedList;
            });
        },
        jumpPage(meetingID, advanceTime) {
            let vm = this;
            console.log(meetingID);
            vm.$router.push('/detailsMeeting/' + meetingID + '/' + advanceTime);
        },
        // 跳转到日程
        jumpSchedule() {
            let vm = this;
            vm.$router.push({
                name: 'mySchedule',
                params: {
                    schId: vm.userInfo.empcode,
                    schFlag: 0,
                    schName: 'my'
                }
            });
        }
    }
}
</script>
<style lang="less" scoped>
.schBg .schedules {
  background: #f8f8f8;
}

.main_sche {
  padding-top: 0.3rem;
}

.newSchdlue {
  width: 2.88rem;
  height: 0.52rem;
  line-height: 0.52rem;
  font-size: 0.26rem;
  background: #a5a5a5;
  color: #fff;
  border-radius: 0.05rem;
  margin: 0.3rem auto;
  span {
    float: left;
  }
  .redCircle {
    width: 0.12rem;
    height: 0.12rem;
    float: left;
    border-radius: 50%;
    background: #ff2a12;
    margin-top: 0.2rem;
    margin-left: 0.18rem;
    margin-right: 0.14rem;
  }
  .text {
    margin-right: 0.2rem;
  }
}

.scheduleDetail {
  position: relative;
  height: auto;
  background: #fff;
  border-radius: 0.12rem;
  padding: 0.3rem 0.1rem 0.08rem;
  margin-bottom: 0.3rem;
  .topMess {
    padding: 0 0.28rem 0rem;
  }
  .status {
    width: 1.06rem;
    height: 0.4rem;
    border-radius: 0.06rem;
    background: #f1f1f1;
    color: #999;
    top: 0.32rem;
    right: 0.46rem;
    line-height: 0.4rem;
    font-size: 0.22rem;
    text-align: center;
    &.now {
      color: #fff;
      background: #ff3358;
    }
  }
  .title {
    font-size: 0.32rem;
    color: #333;
    line-height: 0.46rem;
    height: auto;
    font-weight: bold;
    width: 4.9rem;
    margin-bottom: 0.14rem;
  }
  .title.nosure {
    padding-left: 0.85rem;
    background: url("../../../static/images/conference/noSure.png") 0 .06rem no-repeat;
    background-size:  .73rem .33rem;
  }
  .bottomBox {
    color: #333;
    font-size: 0.28rem;
    ul {
      overflow: hidden;
      padding-left: 0.28rem;
    }
    li {
      height: 0.8rem;
      line-height: 0.8rem;
      float: left;
      width: 1.7rem;
      &.edit {
        background: url("../../../static/images/fujian@2x.png") no-repeat left;
        background-size: 0.3rem;
      }
      &.share {
        background: url("../../../static/images/vip@2x.png") no-repeat left;
        background-size: 0.3rem;
      }
      &.video {
        background: url("../../../static/images/shipin@2x.png") no-repeat left;
        background-size: 0.3rem;
      }
    }
    li:not(:first-child) {
      margin-left: 0.2rem;
    }
    .edit {
      // margin-left: .3rem;
      padding-left: 0.4rem;
    }
    .video,
    .share {
      padding-left: 0.4rem;
    }
  }
}

.yd-icon-unStart:before,
.yd-icon-unAttend:before,
.yd-icon-start:before,
.yd-icon-attend:before {
  display: block;
  content: "";
}

.schedules {
  .yd-icon-unStart:before {
    width: 0.41rem;
    height: 0.41rem;
    background: url(../../../static/images/huiyi1@2x.png) no-repeat 0 0;
    background-size: 0.4rem;
  }
  .yd-icon-start:before {
    width: 0.41rem;
    height: 0.41rem;
    background: url(../../../static/images/huiyi@2x.png) no-repeat 0 0;
    background-size: 0.4rem;
  }
  .yd-icon-unAttend:before {
    width: 0.41rem;
    height: 0.41rem;
    background: url(../../../static/images/jieshu@2x.png) no-repeat 0 0;
    background-size: 0.4rem;
  }
  .yd-icon-attend:before {
    width: 0.41rem;
    height: 0.41rem;
    background: url(../../../static/images/jieshu1@2x.png) no-repeat 0 0;
    background-size: 0.4rem;
  }
  .yd-tabbar-txt {
    color: #59a5fd;
  }
}

.pubList {
  padding-bottom: 0 !important;
}

.tabTopL {
  padding-left: 0.68rem !important;
}

.imgWork {
  margin-top: 0.03rem !important;
}

.noBox {
  padding-top: 2.2rem;
  text-align: center;
  img {
    width: 2.5rem;
    margin-bottom: 0.4rem;
  }
}

.noWord {
  font-size: 0.28rem;
  color: #666;
  text-align: center;
}
</style>


